.project {
  font-family: var(--f-1title);
  margin-left: 5rem;
}

.project-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem;
  margin: 3rem;
}

.portfolio-items {
  background: var(--c-grey);
  padding: 5px;
  border-radius: 3px;
  border: 1px solid transparent;
  transition: var(--c-black);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-decoration: none;
  color: black;
  font-family: var(--f-1title);
}

.portfolio-items:hover {
  background-color: var(--c-lightgrey);
}

img {
  object-fit: cover;
  width: 100%;
  height: 22rem;
}

.project-info {
  background: var(--c-white);
}

.project-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem 0 0;
}

.project-links > ul {
  display: inline-flex;
  gap: 2rem;
  list-style-type: disc;
}

.project-title {
  display: flex;
  grid-gap: 25px;
  gap: 25px;
  padding: 10px 20px;
  align-items: flex-end;
}

.project-title > a {
  margin-left: auto;
  color: var(--c-black);
  font-size: 2rem;
}

.project-title > a:hover {
  color: var(--c-grey);
}

.project-title > h3 {
  margin: 0;
  font-weight: 400;
}
.project-title > h4 {
  margin: 0;
}

@media screen and (max-width: 1024px) {
  .project-container {
    grid-template-columns: 3fr;
    gap: 4rem;
  }
  .portfolio-items {
    justify-content: space-evenly;
  }
  .portfolio-items {
    height: 100%;
  }
}

@media screen and (max-width: 600px) {
  .project {
    margin-left: 15px;
  }

  .project-container {
    gap: 2rem;
    display: flex;
    flex-direction: column;
    margin: 45px 30px;
  }

  .portfolio-items {
    height: 100%;
    justify-content: space-evenly;
  }

  .project-links {
    padding: 0 8px 0 8px;
  }

  .project-links > ul {
    gap: 20px;
    list-style-type: revert;
    font-size: 12px;
    padding-left: 18px;
    flex-wrap: wrap;
  }

  .project-title > a {
    font-size: 22px;
  }
}
