@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@900&family=Fredoka+One&family=Lato:wght@100&family=Nunito:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@900&family=Fredoka+One&family=Lato:wght@100;400&family=Nunito:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@900&family=Fredoka+One&family=Lato:wght@100;400&family=Lekton&family=Nunito:wght@200&display=swap);
:root {
  /* color */
  --c-brown: #bb8d58;
  --c-lightbrown: #ceb394;
  --c-black: #000;
  --c-grey: #505050;
  --c-lightgrey: #787878;
  --c-white: #ffffff;

  /* font-size */
  --f-u8: 8px;
  --f-u10: 10px;
  --f-u15: 15px;
  --f-u17: 17px;
  --f-u20: 20px;
  --f-u25: 25px;
  --f-u30: 30px;
  --f-u40: 40px;
  --f-u50: 50px;
  --f-umax: 8rem;

  /* font-family */
  --f-thin: 'Nunito', sans-serif;
  --f-1title: 'Lato', sans-serif;
  --f-cv: 'Lekton', sans-serif;
}

/* lines */

.main-line {
  display: block;
  height: 1px;
  border: 0;
  border-top: 10px solid #bb8d58;
  border-top: 10px solid var(--c-brown);
}

.small-line {
  display: block;
  height: 1px;
  border: 0;
  border-top: 5px solid #bb8d58;
  border-top: 5px solid var(--c-brown);
}

/* navbar */

body {
  color: #000;
  color: var(--c-black);
  background-color: #f8f7f4;
  position: relative;
  margin: 0;
}

.navbar {
  height: 0;
  width: 100%;
}

.links-navbar > a {
  text-decoration: none;
  color: black;
  border: none;
  background-color: #bb8d58;
  font-family: 'Nunito', sans-serif;
  font-family: var(--f-thin);
  font-size: 20px;
  font-size: var(--f-u20);
}

.links-navbar {
  display: flex;
  justify-content: flex-end;
  grid-gap: 2rem;
  gap: 2rem;
  position: relative;
  padding: 1rem 2rem 0 0;
}

.trapezoid {
  position: absolute;
  width: 16rem;
  height: 4rem;
  background: #bb8d58;
  background: var(--c-brown);
  -webkit-transform: perspective(14px) rotateX(-2deg);
          transform: perspective(14px) rotateX(-2deg);
  right: 0;
  -webkit-transform-origin: right;
          transform-origin: right;
}

/* footer */

.footer {
  text-align: center;
  font-family: 'Nunito', sans-serif;
  font-family: var(--f-thin);
  margin: 6rem 0 1rem;
  bottom: 0;
  left: 0;
  width: 100%;
}

.title-footer {
  font-size: 8px;
  font-size: var(--f-u8);
}

.built-with-footer {
  font-size: 10px;
  font-size: var(--f-u10);
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  padding-bottom: 10px;
  white-space: pre;
}

.built-with-footer > a {
  text-decoration: none;
  color: #000;
  color: var(--c-black);
  top: 2px;
  position: relative;
}

@media screen and (max-width: 1024px) {
  /* footer */
  .built-with-footer {
    font-size: 10px;
    font-size: var(--f-u10);
  }

  .built-with-footer > a {
    font-size: 15px;
    font-size: var(--f-u15);
  }
}

@media screen and (max-width: 600px) {
  /* navbar */
  .trapezoid {
    width: 12rem;
    height: 55px;
  }
  .links-navbar > a {
    font-size: 17px;
    font-size: var(--f-u17);
  }
  .links-navbar {
    padding: 1rem 1rem 0 0;
    grid-gap: 1rem;
    gap: 1rem;
  }

  /* footer */

  .built-with-footer > a {
    font-size: 15px;
    font-size: var(--f-u15);
  }
}

.about {
  color: var(--c-black);
  padding: 5rem;
}

.about-name {
  font-family: var(--f-1title);
  font-size: var(--f-u30);
}

.about-title {
  font-family: var(--f-1title);
  font-size: var(--f-umax);
  font-weight: bold;
}

.about-more {
  padding-top: 3rem;
}

.about-more > a {
  font-family: var(--f-thin);
  font-size: var(--f-u25);
  color: var(--c-black);
  text-decoration: none;
  background-color: var(--c-brown);
  padding: 10px 30px;
}

.about-more > a:hover {
  color: var(--c-grey);
}


@media screen and (max-width: 600px) {
  .about-more {
    font-size: var(--f-u30);
  }

  .about-name {
    font-size: var(--f-40);
  }

  .about-title {
    font-size: var(--f-u40);
  }

  .about {
    padding: 7rem 3rem 4rem 3rem;
  }

  .about-more > a {
    font-size: var(--f-u15);
    padding: 8px 16px;
  }
}

.contact {
  font-family: var(--f-1title);
  margin-left: 15rem;
  bottom: var(--f-umax);
}

.contact h1 {
  font-size: var(--f-u20);
  padding-top: 5rem;
}

.contact-info {
  font-size: var(--f-u15);
  margin-left: 2rem;
  margin-top: 2rem;
  color: var(--c-black);
  font-family: var(--f-thin);
}

.contact-info > h3 > a {
  color: var(--c-black);
}

/* contact form */

.container-form {
  background-color: var(--c-lightbrown);
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  border-radius: 4px;
  width: 90%;
}
.container-form > .form-input {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  justify-content: space-between;
  padding-bottom: 10px;
  text-align: center;
  width: 100%;
}

.text-input {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  width: 100%;
}

.text-input > label > input {
  border: 0;
  padding: 10px;
  border-radius: 3px;
  font-family: var(--f-thin);
  width: 100%;
  box-sizing: border-box;
}

.text-area-input {
  width: 100%;
}

.text-area-input > label > textarea {
  width: 100%;
  border: 0px;
  padding: 10px;
  border-radius: 3px;
  height: 100%;
  box-sizing: border-box;
  font-family: var(--f-thin);
}
.container-form > button {
  font-family: var(--f-thin);
  font-size: var(--f-u15);
  text-decoration: none;
  background-color: var(--c-brown);
  padding: 3px 25px;
  border: 0;
  border-radius: 3px;
}

.container-form > button:hover {
  color: var(--c-white);
}

/* socials */

.socials {
  margin-left: 5rem;
  margin-top: 4rem;
}

.socials h2 {
  font-size: var(--f-u20);
}

ul {
  list-style-type: none;
}

li > a {
  text-decoration: none;
  color: var(--c-black);
  font-size: var(--f-u15);
}

li > a:hover {
  text-decoration: none;
  color: var(--c-grey);
}

@media screen and (max-width: 1024px) {
  .contact {
    margin-left: 5rem;
  }
  .container-form {
    align-items: unset;
    max-width: 30rem;
  }
  .container-form > .form-input {
    flex-direction: column;
  }
}

@media screen and (max-width: 600px) {
  .contact {
    margin-left: 2rem;
  }

  .contact-info {
    margin-left: 0;
    margin-right: 2rem;
  }
}

.project {
  font-family: var(--f-1title);
  margin-left: 5rem;
}

.project-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2.5rem;
  gap: 2.5rem;
  margin: 3rem;
}

.portfolio-items {
  background: var(--c-grey);
  padding: 5px;
  border-radius: 3px;
  border: 1px solid transparent;
  transition: var(--c-black);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-decoration: none;
  color: black;
  font-family: var(--f-1title);
}

.portfolio-items:hover {
  background-color: var(--c-lightgrey);
}

img {
  object-fit: cover;
  width: 100%;
  height: 22rem;
}

.project-info {
  background: var(--c-white);
}

.project-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem 0 0;
}

.project-links > ul {
  display: inline-flex;
  grid-gap: 2rem;
  gap: 2rem;
  list-style-type: disc;
}

.project-title {
  display: flex;
  grid-gap: 25px;
  grid-gap: 25px;
  gap: 25px;
  padding: 10px 20px;
  align-items: flex-end;
}

.project-title > a {
  margin-left: auto;
  color: var(--c-black);
  font-size: 2rem;
}

.project-title > a:hover {
  color: var(--c-grey);
}

.project-title > h3 {
  margin: 0;
  font-weight: 400;
}
.project-title > h4 {
  margin: 0;
}

@media screen and (max-width: 1024px) {
  .project-container {
    grid-template-columns: 3fr;
    grid-gap: 4rem;
    gap: 4rem;
  }
  .portfolio-items {
    justify-content: space-evenly;
  }
  .portfolio-items {
    height: 100%;
  }
}

@media screen and (max-width: 600px) {
  .project {
    margin-left: 15px;
  }

  .project-container {
    grid-gap: 2rem;
    gap: 2rem;
    display: flex;
    flex-direction: column;
    margin: 45px 30px;
  }

  .portfolio-items {
    height: 100%;
    justify-content: space-evenly;
  }

  .project-links {
    padding: 0 8px 0 8px;
  }

  .project-links > ul {
    grid-gap: 20px;
    gap: 20px;
    list-style-type: revert;
    font-size: 12px;
    padding-left: 18px;
    flex-wrap: wrap;
  }

  .project-title > a {
    font-size: 22px;
  }
}

